html {
    background-color: black;
    font-family: 'Times New Roman', serif;
    font-size: 20px;
    margin:0;
    height:auto;
        height: 100%;
    min-height: 100%;
}

.max_width {
  width:100%;
}

#root {
    height: 100%;
    min-height: 100%;
}

.App {
    height: 100%;
    min-height: 100%;
}

.hyperlink {
  color: lightblue;
}

.margin-left {
  margin-left: 50px;
}

.margin-bottom {
  margin-bottom: 10px;
}

.margin-bottom-big {
  margin-bottom: 100px;
}

.margin-right {
  margin-right: 50px;
}

.inline-block {
  display: inline-block;
  vertical-align: top;
}

.cv-home {
  width:700px;
  height: 800px;
}

.cv-pdf {
  width:1200px;
  height: 600px;
}

.cv {
  width: 800px;
  height: 600px;
}

.CNNImg {
  width:200px;
  height:auto;
}

.cert {
  width:600px;
  height:auto;
  
}

.cert-none {
  display: none;
}

.cert-invisible {
  animation: fadeOut 0.5s;
}

.cert-show {
  display: inline-block;
  animation: fadeIn 0.5s;
}

.cert-invisible-prev {
  animation: fadeOutPrev 0.5s;
}

.cert-show-prev {
  display: inline-block;
  animation: fadeInPrev 0.5s;
}

.pageHeading {
  margin-top: 0;
  margin-bottom: 50px;
}

.video {
      width:300px;
      height:300px;
}

#contact_details_wrapper {
    vertical-align:top;
    display: inline-block;
    margin-right:100px;
}

#social_media_wrapper {
    display: inline-block;
}

@keyframes fadeOut {
  0% { opacity: 1; transform: translateX(0);}
  50% { opacity: 0.01; transform: translateX(-5%);}
  100% { opacity: 0; transform: translateX(-10%);}
}

@keyframes fadeIn {
  0% { opacity: 0; transform: translateX(10%);}
  50% { opacity: 0.01; transform: translateX(5%);}
  100% { opacity: 1; transform: translateX(0);}
}

@keyframes fadeOutPrev {
  0% { opacity: 1; transform: translateX(0);}
  50% { opacity: 0.01; transform: translateX(5%);}
  100% { opacity: 0; transform: translateX(10%);}
}

@keyframes fadeInPrev {
  0% { opacity: 0; transform: translateX(-10%);}
  50% { opacity: 0.01; transform: translateX(-5%);}
  100% { opacity: 1; transform: translateX(0);}
}


@media screen and (max-width:1600px) {
  .cv {
    width: 600px;
    height: 600px;
  }
}

@media screen and (max-width:1400px) {
  .cv-pdf {
    width: 800px;
    height: 400px;
  }

.cv-home {
  width:600px;
  height: 800px;
}

  .cv {
    width: 500px;
    height: 600px;
  }

  .projects {
      margin:0;
  }

  .video {
      width:300px;
      height:300px;
  }
}

@media screen and (max-width:1300px) {
    .cert {
      width:400px;
      height:auto;
    }

      .cv-pdf {
        width: 500px;
        height: 500px;
      }

      .CNNImg {
        width:100px;
        height:auto;
      }
}

@media screen and (max-width:700px) {
.video {
    width:200px;
    height:200px;
}

.projects {
    margin:0;
}

.CNNImg {
  width:50px;
  height:auto;
}

  .cert {
    width:200px;
    height:auto;
  }

    .cv-home {
      width: 400px;
      height: 600px;
    }

  .cv {
    position: relative;
    left: -20%;
    width:500px;
    height: 1200px;
    z-index: -1;
    
  }

  .cv-home {
    display:none;
    width: 100%;
    height: 100%;
  }

  #social_media_wrapper {
      display: block;
  }
}

@media screen and (max-height:700px) {
  .pageHeading {
    margin-top: 0;
    margin-bottom: 30px;
  }
}